.timer-container {
    max-width: 500px;
    margin: 50px auto;
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    font-family: 'Arial', sans-serif;
  }
  
  .timer-display {
    margin-bottom: 30px;
  }
  
  .timer-display h1 {
    font-size: 3.5rem;
    color: #333;
    margin: 0;
  }
  
  .timer-controls {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 20px;
  }
  
  .timer-btn {
    padding: 10px 25px;
    font-size: 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .start-btn {
    background-color: #4CAF50;
    color: white;
  }
  
  .start-btn:hover {
    background-color: #45a049;
  }
  
  .stop-btn {
    background-color: #f44336;
    color: white;
  }
  
  .stop-btn:hover {
    background-color: #d32f2f;
  }
  
  .reset-btn {
    background-color: #2196F3;
    color: white;
  }
  
  .reset-btn:hover {
    background-color: #0b7dda;
  }
  
  .timer-modes {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 20px;
  }
  
  .mode-btn {
    padding: 8px 20px;
    background-color: #e0e0e0;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .mode-btn.active {
    background-color: #9e9e9e;
    color: white;
  }
  
  .mode-btn:hover {
    background-color: #bdbdbd;
  }
  
  .countdown-settings {
    margin-top: 20px;
  }
  
  .countdown-settings label {
    display: block;
    margin-bottom: 8px;
    font-weight: bold;
  }
  
  .countdown-settings input {
    padding: 8px;
    width: 60px;
    text-align: center;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
  }